import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
// 
// 
const store = new Vuex.Store({
  //  app
  state: { 
    supManTab: 0,//list /
    isEmable:false,///,
    imgLen:[]
  },
    mutations: {
        setSupManTab(state,tab){
          state.isEmable=true
          state.supManTab=tab
        },
        getSupManTab(state){
          state.isEmable=false
        },// 
        getImgLen(state,data,dataMonted){
          data&&data.length&& state.imgLen.push(...data)
          if(dataMonted&&dataMonted.length){
            state.imgLen=dataMonted
          }
        }
    },
    actions: {

    },

})
//  store
//  store 
export default store;
